
*{
  box-sizing: border-box;
  transition:all .3s ease-in-out;
}

html{
  font-size: 16px;
}

section{
  text-align: center;
  padding:20px;
}

section h1{
  margin-bottom: 20px;
  font-size:1.4rem;
  font-weight: bold;
}


.ordered_list{
  max-width: 1000px;
  height:auto;
  margin:0 auto;
}

.list_block{
display: flex;
  border:1px solid black;
  padding:10px;
  margin-bottom: 20px;
  flex-direction: column;
  min-width: 300px;
}
label{
width:100%;
margin-bottom: 5px!important;
padding-bottom: 5px!important;
border-bottom: .3px solid rgba(0, 0, 0, 0.329);
}

input{
margin-right: 10px;
  padding:10px;
}

.question_wrapper{
display: flex;
text-align: center;
flex-direction: row;
justify-content: flex-start;
align-items: center;
margin-bottom: 10px;
}

.question{
  font-weight: bold;
  margin-bottom: 5px;
  text-align: left;
  font-size: 1.2rem;
  box-shadow: 0 4px 2px -2px gray;
  margin:-10px;
  margin-bottom: 20px;
  padding: 10px;
 
  background: rgb(245, 239, 239);
}
.choice{
  text-align: left;


}

.check_results{
  padding:10px 20px;
  font-size: 1.2rem;
}
@media only screen and (max-width: 600px) {
  html{
    font-size: 14px;
  }
}